import axios from 'axios';

export async function formHandle(form, FormItem, methodType='POST', ID, onProgressUpdate){
    
    const { callBack, formName } = form;
    let url;
    let options;
    let formdat;
    if(methodType === 'POST'){
        let forms = new FormData(FormItem);
        forms.append('Token', localStorage.getItem('Token'));
        forms.append(formName, '');

        options = {
            method: methodType,
            body: forms
        }

        formdat = forms;
        
    }else{
        let FormOut = {};
        for (let i = 0; i < FormItem.length; i++) {
            const item = FormItem[i];            
            FormOut[item.name] = item.value;
        }

        const token = localStorage.getItem('Token');
        FormOut['Token'] = token;

        options = {
            method: methodType,
            headers:{
            'Content-Type': '"application/x-form-urlencoded"'
            },
            body: JSON.stringify(FormOut)
        }
        formdat = FormOut;
    }
    console.log(callBack)


    if(ID){
         url = callBack+'/'+ID
    }else{
         url = callBack
    }
   // const res = await fetch(url, options);

    const res2 = await axios.post(url, formdat, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
        onUploadProgress: (progressEvent) => {
            const progress = (progressEvent.loaded / progressEvent.total) * 50;
            if (onProgressUpdate) {
              onProgressUpdate(progress); // Call the callback function
            }
        }});
    console.log(res2)
    
    //const Response = await res.json();
    //return Response
    
}
  